@import "src/styles/global.scss";
.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  width: 100%;

  &__svg {
    animation: rotation 1s linear infinite;
  }
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}